
import Vue from 'vue';

import SnackbarComponent from '../../components/SnackbarComponent.vue';
import LoaderComponent from '../../components/LoaderComponent.vue';

export default Vue.extend({
  name: 'EmptyLayout',

  components: {
    SnackbarComponent,
    LoaderComponent,
  },

  data: () => ({
    loading: true,
  }),

  async mounted() {
    this.loading = false;
  },
