
import Vue from 'vue';

import DefaultLayout from './views/layouts/DefaultLayout.vue';
import EmptyLayout from './views/layouts/EmptyLayout.vue';

export default Vue.extend({
  name: 'App',
  components: {
    DefaultLayout,
    EmptyLayout,
  },
  computed: {
    layout() {
      return this.$route.meta.layout;
    },
  },
});
